<template>
<el-dialog 
  :title="editIndex>=0?'':''"
  :visible.sync="dialogFormVisible"
  :before-close="handleClose"
  :close-on-click-modal="false"
  width="30%">
  <el-form :model="form" ref="form" size="mini" :rules="rules">
    <el-form-item label="" label-width="120px" prop="create_date">
        <el-date-picker
        v-model="form.create_date"
        type="date"
        :disabled="true"
        format="yyyy  MM  dd "
        value-format="timestamp"
        placeholder="">
        </el-date-picker>
    </el-form-item>
    <el-form-item label="" label-width="120px" prop="prjname">
      <el-input v-model="form.prjname"
                placeholder=""
                autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" label-width="120px" prop="region">
      <el-input v-model="form.region"
                placeholder=""
                autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" label-width="120px" prop="prjmodel">
        <el-select
            v-model="form.prjmodel"
            multiple
            filterable
            allow-create
            default-first-option
            style="width:100%"
            placeholder="">
        </el-select>
    </el-form-item>
    <el-form-item label="" label-width="120px" prop="prjtype">
        <el-select
            v-model="form.prjtype"
            multiple
            filterable
            allow-create
            default-first-option
            style="width:100%"
            placeholder="">
        <el-option
            v-for="item in itemTypeList"
            :key="item"
            :label="item"
            :value="item">
        </el-option>
        </el-select>
    </el-form-item>
    <el-form-item label="" label-width="120px" prop="brief">
      <el-input type="textarea"
                v-model="form.brief"
                placeholder="/"
                autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="svn/git" label-width="120px">
      <el-input v-model="form.svnurl"
                placeholder=""
                autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="()" label-width="120px">
        <el-input-number
        v-model="form.period" 
        :min="1"
        :disabled="editIndex>=0"
        label="">
        </el-input-number>
    </el-form-item>
    <el-form-item label="/" label-width="120px">
        <!--el-col :span="18">
            <el-slider :disabled="true" v-model="form.percent" @change="percentChange(form.percent)"></el-slider>
        </el-col-->
        <el-col :span="24">
            <el-select @change=statusChange v-model="form.status" placeholder="">
                <el-option label="" value=""> </el-option>
                <el-option label="" value=""> </el-option>
                <el-option label="" value=""> </el-option>
                <el-option label="" value=""> </el-option>
            </el-select>
        </el-col>
    </el-form-item>
    <el-form-item label="" label-width="120px" prop="duty_persons">
        <el-select
            v-model="form.duty_persons"
            multiple
            filterable
            allow-create
            default-first-option
            style="width:100%"
            placeholder="">
        <el-option
            v-for="item in personList"
            :key="item"
            :label="item"
            :value="item">
        </el-option>
        </el-select>
    </el-form-item>
    <el-form-item label="" label-width="120px">
        <el-select
            v-model="form.relate_persons"
            multiple
            filterable
            allow-create
            default-first-option
            style="width:100%"
            placeholder=""></el-select>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="Cancel"> </el-button>
    <el-button type="primary" @click="Confirm('form')"> </el-button>
  </div>
</el-dialog>
</template>

<script>
export default {
    name: 'item_edit',
    data() {
        var statusCheck = (rule, value, callback) => {
            
            if(['','','', ''].indexOf(value) !== -1)
            {
                //correct
                return true;
            }
            else
            {
                //err
                return callback(new Error("'','','', ''"));
            }
        }

        return {
            /*  */
            dialogFormVisible: false,
            /*  */
            itemTypeList: ['','','TVD','RV_TVD','HEOP'],
            /*  */
            personList:[],
            /*  */
            form: {
                relate_persons:[],
                duty_persons:[]
            },
            /*  */
            rules: {
                create_date: [
                    { required: true, message: '', trigger: 'change' }
                ],
                prjname: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                brief: [
                    { required: true, message: '/', trigger: 'blur' }
                ],
                region: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                period: [
                    { required: true, type: 'integer', min: 1, max: 100, message: '1', trigger: 'blur' }
                ],
                percent: [
                    { required: true, type: 'integer', min: 0, max: 100, message: '0-100', trigger: 'blur' }
                ],
                status: [
                    { validator: statusCheck, trigger: 'blur' }
                ],
                prjtype: [
                    { required: true, type: 'array', min: 1, message: '', trigger: 'blur' }
                ],
                duty_persons: [
                    { required: true, type: 'array', min: 1, message: '', trigger: 'blur' }
                ]
            }
        }
    },
    props: {
        /**/
        editIndex:{
            type: Number,
            default: -1
        },
        /**/
        value:{
            type: Object,
            validator: function (value) {
                //todo:
                return true
            }
        }
    },
    components: {},
    computed: {},
    watch: {
    },
    methods: {
        statusChange()
        {
            /*
            if(this.form.status == "")
            {
                this.form.percent = 100;
            }*/
        },
        percentChange(percent){
            if(percent == 100)
            {
                this.form.status = "";
            }
            else
            {
                this.form.status = "";
            }
        },
        handleClose(done) {
            this.dialogFormVisible = false;
            this.$emit("dialog-close");
        },
        Cancel(){
            this.dialogFormVisible = false;
            this.$emit("dialog-close");
        },
        Confirm(formValue){
            this.$refs[formValue].validate((valid) => {
                if (valid) {
                    this.dialogFormVisible = false;
                    this.$emit("dialog-submit",this.form,this.form.uuid);
                } else {
                    return false;
                }
            });
        }
    },
    created() {},
    mounted() {
        //deep copy
        this.form = Object.assign({},this.value);
        this.dialogFormVisible = true;
    },
    updated() {
    },
    destroyed() {}
}
</script>

<style scoped>
    .el-slider >>> .el-slider__runway {
        margin: 11px 0;
    }

    .el-slider {
        width: 90%;
    }

    .el-form-item{
        text-align: left;
    }
</style>


