<!---->
<template>
<el-dialog 
  :title="headLable"
  :show-close="false"
  :visible.sync="formVisible"
  :before-close="handleClose"
  :close-on-click-modal="false"
  width="30vw">
  <el-form :model="form" ref="form" size="mini" :rules="rules">
    <el-form-item label="" label-width="100px" prop="checkedOption">
        <el-checkbox :indeterminate="isIndeterminate" 
                     v-model="form.checkAll" 
                     @change="handleCheckAllChange"></el-checkbox>
        <div style="margin: 15px 0;"></div>
        <el-checkbox-group v-model="form.checkedOption" 
                           @change="exportOptChange">
            <el-checkbox v-for="opt in exportOption" :label="opt['name']" :key="opt['name']">{{ opt['name'] }}</el-checkbox>
        </el-checkbox-group>
    </el-form-item>
    <el-form-item v-if="isContent" label="" label-width="100px" prop="isWeekRange">
        <el-switch v-model="form.isWeekRange"></el-switch>
    </el-form-item>
    <el-form-item v-if="isContent" label="" label-width="100px" prop="contentTimeRange">
        <el-date-picker
            v-model="form.contentTimeRange"
            type="daterange"
            align="right"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="pickerOptions">
        </el-date-picker>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="Cancel"> </el-button>
    <el-button type="primary" @click="Confirm"></el-button>
  </div>
</el-dialog>
</template>

<script>
import {date2shortStr} from '@/assets/js/common.js'

export default {
    name: 'exportOptionForm',
    data() {
        let checkOpt = (rule, value, callback)=>{
            if(value.length<=0) {
                callback(new Error(''));
            }
            callback();
            return true;
        }

        return {
            /*  */
            formVisible: true,
            /*  */
            isContent: true,
            /* , */
            isIndeterminate:false,
            /*  */
            form: {
                /*  */
                checkAll: true,
                /*  */
                checkedOption:[],
                /*  */
                isWeekRange: true,
                /*  */
                contentTimeRange:[new Date(0),new Date()]
            },
            /*  */
            rules: {
                checkedOption: [
                    { required: true, validator: checkOpt, trigger: 'blur' }
                ]
            },
            /*  */
            pickerOptions: {
                shortcuts: [{
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(0);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            }
        }
    },
    props: {
        /**/
        headLable:{
            type: String,
            required: true,
        },
        /**/
        exportOption:{
            type: Object,
            default: null,
            validator: function (value) {
                //todo:
                return true
            }
        },
        /*  */
        exportAction:{
            type: Function,
            required: true,
        }
    },
    components: {},
    computed: {},
    watch: {},
    methods: {
        /**
         * @description: 
         * @param {Boolean} val 
         */         
        handleCheckAllChange(val) {
            if(val)
            {
                for(let key in this.exportOption)
                {
                    this.form.checkedOption.push(this.exportOption[key]["name"]);
                }

                this.isContent = true;
            }
            else
            {
                this.form.checkedOption = [];
            }
            this.isIndeterminate = false;
        },
        /**
         * @description: 
         * @param {Array} val 
         */    
        exportOptChange(val) {
            let checkedCount = val.length;
            let total_opt_num = 0;

            for(let key in this.exportOption)
            {
                total_opt_num++;
            }
            this.checkAll = checkedCount === total_opt_num;
            this.isIndeterminate = checkedCount > 0 && checkedCount < total_opt_num;

            if(this.form.checkedOption.indexOf("") > -1)
            {
                this.isContent = true;
            }
            else
            {
                this.isContent = false;
            }
        },
        /**
         * @description: 
         * @param {Object} done 
         */
        handleClose(done) {
            this.formVisible = false;
            this.$emit("dialog-close")
            .then(() => {
                done();
            })
            .catch(() => {});
        },
        /**
         * @description: 
         * @event dialog-close 
         */
        Cancel(){
            this.formVisible = false;
            this.$emit("dialog-close");
        },
        /**
         * @description: 
         * @event dialog-submit 
         */
        Confirm(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    this.formVisible = false;
                    let columns = this.exportOption;
                    let dateRange = [new Date(1970,0,0),new Date()];

                    //
                    for(let key in columns)
                    {
                        if(!(this.form.checkedOption.indexOf(columns[key].name) > -1))
                        {
                            delete columns[key];
                        }
                        else
                        {
                            //
                            if(this.form.checkedOption.indexOf("") > -1 
                                && columns[key].name == "")
                            {
                                let wpx = columns[key].wpx;
                                let alignment = columns[key].alignment;

                                dateRange[0] = columns[key].dateRange[0].getTime() >
                                                this.form.contentTimeRange[0].getTime()?
                                                columns[key].dateRange[0]:this.form.contentTimeRange[0];
                                if(this.form.isWeekRange)
                                {
                                    //
                                    let DayOfWeek = this.form.contentTimeRange[1].getDay(); //
                                    let Day = this.form.contentTimeRange[1].getDate(); //
                                    let Month = this.form.contentTimeRange[1].getMonth(); //
                                    let Year = this.form.contentTimeRange[1].getFullYear(); //

                                    dateRange[1] = new Date(Year, Month, Day - DayOfWeek+7);

                                    DayOfWeek = dateRange[0].getDay(); //
                                    Day = dateRange[0].getDate(); //
                                    Month = dateRange[0].getMonth(); //
                                    Year = dateRange[0].getFullYear(); //

                                    dateRange[0] = new Date(Year, Month, Day - DayOfWeek);
                                }
                                else
                                {
                                    dateRange[1] = this.form.contentTimeRange[1];
                                }

                                //
                                for(let date = dateRange[1];date.getTime()>=dateRange[0].getTime();)
                                {
                                    //
                                    let nowDay = date.getDate();
                                    let nowMonth = date.getMonth();
                                    let nowYear = date.getFullYear();
                                    let timeName;
                                    let nextDate;

                                    if(this.form.isWeekRange)
                                    {
                                        timeName = date2shortStr(new Date(nowYear, nowMonth, nowDay-2));
                                    }
                                    else
                                    {
                                        timeName = date2shortStr(date);
                                    }

                                    columns[timeName]=new Object();
                                    columns[timeName].name = timeName;
                                    columns[timeName].wpx = wpx;
                                    columns[timeName].alignment = alignment;

                                    if(this.form.isWeekRange)
                                    {
                                        nextDate = new Date(nowYear, nowMonth, nowDay-7);
                                    }
                                    else
                                    {
                                        nextDate = new Date(nowYear, nowMonth, nowDay-1);
                                    }

                                    columns[timeName].dateRange = [nextDate,date];
                                    date=nextDate;
                                }

                                delete columns[key];
                            }
                        }
                    }
                    //                
                    this.exportAction(columns,dateRange);

                    this.$emit("dialog-submit");
                } else {
                    return false;
                }
            });
        }
    },
    created() {},
    mounted() {
        //deep copy
        if(this.exportOption != null)
        {
            for(let key in this.exportOption)
            {
                this.form.checkedOption.push(this.exportOption[key]["name"]);
            }
        }
        this.formVisible = true;
    },
    updated() {
    },
    destroyed() {}
}
</script>

<style scoped>
    .el-form-item{
        text-align: left;
    }
</style>


