
<template>
<el-dialog 
  :title="headLable"
  :show-close="false"
  :visible.sync="dialogFormVisible"
  :before-close="handleClose"
  :close-on-click-modal="false"
  width="30vw">
  <el-form :model="form" ref="form" size="mini" :rules="rules">
    <el-form-item label="" label-width="100px" prop="username">
        <el-input v-model="form.username"
                :disabled="headLable==''"
                placeholder=""
                autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item v-if="headLable=='' && form.curPasswd" label="" label-width="100px" prop="passwd">
        <el-input v-model="form.curPasswd"
                  show-password
                  placeholder=""
                  autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" label-width="100px" prop="newPasswd">
        <el-input v-model="form.newPasswd"
                  show-password
                  placeholder=""
                  autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" label-width="100px" prop="confirm">
        <el-input v-model="form.confirm"
                  show-password
                  placeholder=""
                  autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item v-if="headLable==''" prop="user_type" label="" label-width="100px">
        <el-select
            v-model="form.user_type"
            style="width:100%"
            placeholder="">
                <el-option label="" value="controller"> </el-option>
                <el-option label="" value="normalizer"> </el-option>
            </el-select>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button v-if="headLable!=''" @click="Cancel"> </el-button>
    <el-button type="primary" @click="Confirm()"> </el-button>
  </div>
</el-dialog>
</template>

<script>
import axios from 'axios'
import CryptoJS from 'crypto-js/crypto-js'

export default {
    name: 'user_edit',
    data() {
        let newPasswd = (rule, value, callback) => {
            if(!value) {
                callback(new Error(''));
            }
            else if(this.form.passwd == value) {
                callback(new Error(''));
            }

            callback();
            return true;
        }
        let confirmPasswd = (rule, value, callback) => {
            if(!value) {
                callback(new Error(''));
            }
            else if(this.form.newPasswd != value) {
                callback(new Error(''));
            }

            callback();
            return true;
        };

        return {
            /*  */
            form: {
                /*  */
                username:"",
                /*  */
                passwd:"",
                /*  */
                newPasswd:"",
                /*  */
                confirm:"",
                /*  */
                user_type:""
            },
            /*  */
            rules: {
                username: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                passwd: [
                    { min: 1,required: true, message: '', trigger: 'blur' }
                ],
                newPasswd: [
                    { required: true, validator: newPasswd, trigger: 'blur' }
                ],
                user_type: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                confirm: [
                    { required: true, validator: confirmPasswd, trigger: 'blur' }
                ]
            }
        }
    },
    props: {
        /*  */
        dialogFormVisible:{
            type: Boolean,
            required: true
        },
        /**/
        headLable:{
            type: String,
            default: "",
            validator: function (value) {
                return ['','',''].indexOf(value) !== -1
            }
        },
        /**/
        value:{
            type: Object,
            default: null,
            validator: function (value) {
                //todo:
                return true
            }
        }
    },
    components: {},
    computed: {},
    watch: {},
    methods: {
        handleClose(done) {
            if(this.headLable == '')
            {
                //root
                return;
            }
            this.$emit("dialog-close")
            .then(() => {
                done();
            })
            .catch(() => {});
        },
        Cancel(){
            if(this.headLable == '')
            {
                //root
                return;
            }
            this.$emit("dialog-close");
        },
        Confirm(){
            this.$refs['form'].validate((valid) => {
                
                if (valid)
                {
                    let form = new Object();

                    // TODO
                    form.username = this.form.username;
                    form.passwd = CryptoJS.SHA256(this.form.newPasswd).toString();
                    /*  */
                    form.prop = this.value.user_type;

                    /* call backend */
                    axios({
                        url:'/user',
                        method: 'put',
                        timeout: 5000,
                        responseType: 'json',
                        responseEncoding: 'utf8', 
                        headers: {
                            'Content-Type': 'application/json;charset=UTF-8'
                        },
                        data:form
                    }).then((res) => {
                        // 
                        if(!res.data.ret)
                        {
                            this.$message({
                                type: 'error',
                                message: ''
                            });
                        }
                        this.$emit("dialog-submit",this.form);
                    }).catch((res)=>{
                        this.$message({
                            type: 'error',
                            message: res
                        });
                        //test
                        this.$emit("dialog-submit",this.form);
                    });
                } else {
                    return false;
                }
            });
        }
    },
    created() {},
    mounted() {
        //deep copy
        if(this.value != null) {
            this.form = Object.assign({},this.value);
        }
    },
    updated() {
    },
    destroyed() {}
}
</script>

<style scoped>
    .el-form-item{
        text-align: left;
    }
</style>


