<!---->
<template>
    <el-progress
        type="line"
        :percentage="percent"
        :color="color"
        :format="progress_content">
    </el-progress>
</template>

<script>
export default {
    name:"process_line",
    data:function(){
        return{}
    },
    props: {
        /*  */
        percent: {
            type: Number,
            default: 10,
            validator: function (value) {
                return value<=100 && value >=0;
            }
        },
        /*  */
        status: {
            type: String,
            default: "",
            validator: function (value) {
                return ['','','',''].indexOf(value) !== -1
            }
        }
    },
    computed: {
        color(){ 
            switch(this.status){
                case '': return '#67C23A';
                case '': return '#409EFF';
                case '': return '#E6A23C';
                case '': return '#F56C6C';
            }
        }
    },
    watch: {},
    methods: {
        progress_content(percentage){
            return `${percentage}%`+this.status;
        }
    },
    created() {},
    mounted() {},
    updated() {},
    destroyed() {}
}
</script>



